body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
    --vert : #6F7835;
    --top : #8A7E66;
    --disable : #CCC;
}
.vert{
    background-color: #6F7835;
    background-color: var(--vert);
}
.vert{
    background-color: #8A7E66;
    background-color: var(--top);
}
.item{
    position: relative;
    height: 500px;
    width: 100%;
    overflow: hidden;
}
/* Img */
.item .img{
    position: absolute;
    width: 100%;
    height: 100%;
    background-position: center;
    background-size: cover;
    z-index: 0;

}

/* ================================= Content ================================*/
.item .content{
    position: absolute;
    width: 100%;
    height: 100%;
    padding-top: 20px;
    padding-bottom: 20px;
    background-color:#6f783568;
    z-index: 1;
}
/* Logo */
.item .content .logo{
   position: relative;
   width: 90px;
   box-shadow: 0px 0px 10px black;
}
/* Menu  */
.item .content .menu{
    position: relative;
    height: 100%;
    justify-content: space-evenly;
    align-items: center;
}
.item .content .menu .menu-item{
    position: relative;
    font-size: 1.1rem;
    text-decoration: none;
    color: white;
}
.item .content .menu .menu-item:hover{
    font-weight: 900;
}
.item .content .menu .menu-item{
    position: relative;
    font-size: 1.1rem;
    text-decoration: none;
    color: white;
}

/* reservation  */
.item .content .reservation{
    position: relative;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 100%;
}
/* Footer */
.item .content .Footer{
    position: relative;
    padding-top:30px ;
    height: 100%;
}
/* Partie 1 */
.item .content .Footer .partie1{
    position: relative;
    height: 100%;
}
/* Etiquette */
.item .content .Footer .etiquette{
    position: relative;
    display: inline-block;
    width:-webkit-fit-content;
    width:-moz-fit-content;
    width:fit-content;
    background-color: #8A7E66;
    background-color: var(--top);
    padding: 7px;
    color: white;
    font-size: 1rem;
   box-shadow: 0px 0px 10px black;

}
.item .content .Footer .etiquette .disable{
    color: #CCC;
    color: var(--disable);
}
/* Title */
.item .content .Footer .title{
   position: relative;
   color: white;
   width: 60%;
   font-size: 4rem;
   line-height: calc(4rem*1.1);
   font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
   text-shadow: 0px 0px 10px black;

}

/* Subtitle */
.item .content .Footer .subtitle{
   position: relative;
   color: white;
   font-size: 1rem;
   margin-top: -10px;
   text-shadow: 0px 0px 10px black;
}
.item .content .Footer .subtitle .header{
   font-size: 1.5rem;
}
/* Partie 2 */
.item .content .Footer .partie2{
    position: relative;
    height: 100%;
    width: 100%;
    flex-direction: column;
    align-items: stretch;
}
/* Control */
.item .content .Footer .control{
    position: relative;
    width: 100%;
    align-items: flex-end;
    padding-right: 20px;
}
.item .content .Footer .control button{
    position: relative;
    width: 1px;
    height: 30px;
    border: 1px solid white;
    border-radius: 3px;
    box-shadow: 0px 0px 10px black;
    background-color: transparent;
}

/* Control */
.item .content .Footer .social{
    position: relative;
    display: flex;
    width: 100%;
    align-items: flex-end;
}



:root {
  --vert: #6f7835;
  --top: #8a7e66;
  --disable: #ccc;
}
.client_section .client_container {
  display: flex;
  flex-direction: column;
  margin: auto;
  margin-top: 45px;
}

.client_section .client_container .client_id {
  display: flex;
  align-items: center;
}

.client_section .client_container .client_id h5 {
  color: #1d1b28;
  font-weight: 600;
  margin-bottom: 5px;
}

.client_section .client_container .client_id h6 {
  margin-bottom: 0;
}

.client_section .client_container .img-box {
  position: relative;
  width: 100px;
  height: 100px;
  border-radius: 100%;
  margin-right: 15px;
  background-position: center;
  background-size: cover;
}

.client_section .client_container .detail-box {
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.2);
  padding: 25px;
  margin-bottom: 25px;
  border-radius: 25px;
  background: #6f7835;
  background: var(--vert);
  color: #ffffff;
}

.client_section .client_container .detail-box span {
  margin-top: 25px;
  color: #8a7e66;
  color: var(--top);
  font-size: 28px;
}


:root {
  --vert: #6f7835;
  --top: #8a7e66;
  --disable: #ccc;
}
.info_section {
  background-color: #6f7835;
  background-color: var(--vert);
  color: #ffffff;
  padding: 45px 0 15px 0;
}

.info_section h4 {
  font-weight: 600;
  margin-bottom: 20px;
}

.info_section .info-col {
  margin-bottom: 30px;
}

.info_section .info_contact .contact_link_box {
  display: flex;
  flex-direction: column;
}

.info_section .info_contact .contact_link_box a {
  margin: 5px 0;
  color: #ffffff;
}

.info_section .info_contact .contact_link_box a i {
  margin-right: 5px;
}

.info_section .info_contact .contact_link_box a:hover {
  color: #8a7e66;
  color: var(--top);
}


.info_section .info_social a:hover {
  color: #8a7e66;
  color: var(--top);
}

.info_section form input {
  border: none;
  padding: 10px 15px;
  background-color: #f7f8f9;
  width: 100%;
  height: 45px;
  color: #000000;
  outline: none;
  border-radius: 45px;
}

.info_section form input::-webkit-input-placeholder {
  color: #556;
}

.info_section form input:-ms-input-placeholder {
  color: #556;
}

.info_section form input::placeholder {
  color: #556;
}

.info_section form button {
  width: 100%;
  text-align: center;
  display: inline-block;
  padding: 10px 55px;
  background-color: #8a7e66;
  background-color: var(--top);
  color: #ffffff;
  border-radius: 45px;
  border: 1px solid white;
  transition: all 0.3s;
  margin-top: 15px;
}

.info_section form button:hover {
  background-color: transparent;
  color: white;
}

.info_section .map_container {
  width: 100%;
  height: 210px;
  overflow: hidden;
  display: flex;
  align-items: stretch;
}

/* footer section*/
.footer_section {
  position: relative;
  background-color: #ffffff;
  text-align: center;
}

.footer_section p {
  color: #6f7835;
  color: var(--vert);
  padding: 25px 0;
  margin: 0;
}

.footer_section p a {
  color: inherit;
}

